@import 'arui-feather/vars.css';

.sms-sign-phone-hint {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__header {
        text-align: center;
        margin: 0;
    }

    &__phones {
        margin-top: 24px;
        display: flex;
        flex-direction: column;
    }

    &__hint {
        margin-top: 24px;
    }

    &__phone {
        margin-top: 12px;

        &:first-child {
            margin-top: 0;
        }
    }

    &__phone-hint {
        opacity: var(--opacity-minor);
    }

    &__hint,
    &__phone-hint,
    &__phone {
        text-align: center;
    }

    &__repeat-button {
        margin-top: 24px;
    }
}
