.bank-contacts {
    display: flex;
    flex-direction: column;

    @media (--small-only) {
        margin-top: 0;
    }

    & .label {
        opacity: 0.6;
        margin-bottom: 12px;

        @media (--small-only) {
            display: none;
        }
    }

    &__title {
        text-align: center;
        margin-bottom: 10px;
    }

    &__wrapper {
        display: flex;
        justify-content: center;
        width: 100%;
    }

    &__item {
        display: flex;
        flex-direction: column;

        &_main {
            & .link .icon_name_call {
                display: none;
            }
        }

        @media (--medium) {
            &:not(:nth-last-child(1)) {
                margin-right: 30px;
            }
        }

        @media (--small-only) {
            display: none;

            &_main {
                display: block;

                & .link {
                    line-height: 0;
                    font-size: 0;
                    height: 22px;
                    width: 18px;
                    position: absolute;
                    right: 12px;
                    top: 12px;

                    .icon_name_call {
                        display: block;
                    }
                }
            }
        }
    }
}
