@import 'arui-feather/vars.css';

.sms-sign-confirmation {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__header {
        text-align: center;
        margin: 0;
    }

    &__input-container {
        margin-top: 24px;
        width: 162px;
        position: relative;
        display: flex;
        align-items: center;
    }

    &__input .input {
        &__box {
            border-bottom-width: 2px;
        }

        &__control {
            font-size: 30px;
            text-align: center;
        }

        &__sub {
            display: none;
        }
    }

    &__error {
        margin: 5px 0 0;
        text-align: center;
        color: var(--color-error);
    }

    &__spin {
        margin-top: 32px;
    }

    &__countdown {
        margin-top: 32px;
        text-align: center;

        &_hidden {
            display: none;
        }

        & .label {
            opacity: var(--opacity-minor);
        }
    }

    &__retry-hint {
        margin-top: 24px;
        opacity: var(--opacity-default);
        text-decoration: underline;
    }
}
